import React from 'react';
import {Button, message, Modal, Table} from "antd";
import CreateNewClass from "./Modal/CreateNewClass";
import {vipList, deleteVip} from "../../../api/user";
import CustomPagination from "../../../components/Layout/Pagination";

class Membership extends React.Component{
	constructor(props) {
		super(props);
		this.state = {
			api: vipList,
			data: [],
			addNewClassVisible: false,
			record: ''
		};
		this.child = React.createRef();
		this.modal = React.createRef();
	}
	
	showModal = (boolean) => {
		this.setState({addNewClassVisible: true, isEdit: boolean})
	};
	hideModal = () => {
		this.setState({addNewClassVisible: false})
	};
	
	refresh = () => {
		this.child.current.pagination(this.child.current.state.current)
	};
	
	// /
	submit = (api,id, name, num, discount) => {
		api({
			grade_name: name,
			score_num: num,
			discount
		},id).then(r=>{
			message.success(r.message);
			this.hideModal();
			this.modal.current.init();
			this.refresh();
		}).catch(_=>{})
	};
	
	edit = (record) => {
		this.setState({record},()=>{
			this.showModal(true)
		})
	};
	
	delete = (id) => {
		let refresh = this.refresh;
		let confirmModal = Modal.confirm({
			title: (
				<div className= 'u_confirm_header'>
					
					<i className="iconfont" style={{'cursor':'pointer'}} onClick={()=>{
						confirmModal.destroy()
					}}>&#xe82a;</i>
				</div>
			),
			icon:null,
			width:'280px',
			closable:true,
			centered:true,
			content: (
				<div className="U_confirm">
					
				</div>
			),
			cancelText: '',
			okText:'',
			okButtonProps: {
				size:'small'
			},
			cancelButtonProps:{
				size:'small'
			},
			onOk() {
				// 
				deleteVip({}, id).then(r=>{
					message.success(r.message);
					refresh()
				}).catch(_=>{})
			}
		});
	};
	
	// 
	paginationChange = (list) =>{
		this.setState({data:list})
	};
	
	render(){
		const columns = [
			{
				title: '',
				dataIndex: 'grade_name',
			},
			{
				title: '',
				dataIndex: 'score_num',
			},
			{
				title: '',
				dataIndex: 'discount',
			},
			{
				title: '',
				render: (text,record) =>
					<div>
						<span
							style={{'color':'#4F9863','cursor':'pointer','marginRight': '20px'}}
							onClick={()=>this.edit(record)}
						>
						</span>
						<span
							style={{'color':'#4F9863','cursor':'pointer'}}
							onClick={()=>this.delete(record.id)}
						>
						</span>
					</div>
			},
		];
		const classProps = {
			visible: this.state.addNewClassVisible,
			onCancel: this.hideModal,
			onSubmit: this.submit,
			record: this.state.record,
			isEdit: this.state.isEdit
		};
		return (
			<div className='membership'>
				<CreateNewClass {...classProps} ref={this.modal} />
				
				
				<div className="header">
					<Button size="small" className="btn btnAdd" onClick={()=>this.showModal(false)} >
						<i className="iconfont" style={{color:'#4F9863',fontSize:'12px',marginRight:'6px'}}>&#xe7e0;</i>
						
					</Button>
					<span></span>
				</div>
				<div className="chart">
					<Table
						columns={columns}
						rowKey={record => record.id}
						pagination={false}
						rowClassName={(record, index) => {
							let className = '';
							if (index % 2 ) className = 'dark-row';
							return className;
						}}
						dataSource={this.state.data}
					/>
				</div>
				<div className="pagination">
					<CustomPagination
						api={this.state.api}
						ref={this.child}
						valChange={this.paginationChange}
						text={''}
					/>
				</div>
			</div>
		)
	}
}
export default Membership
