import React, {Component,Fragment} from 'react';
import {Button, Input, ConfigProvider, Select, Table,DatePicker} from "antd";
import zh_CN from "antd/lib/locale-provider/zh_CN";
import CustomPagination from "../../../../components/Layout/Pagination";
import '../css/consumerRefund.sass'
import {withRouter} from 'react-router-dom'
import {refundRecords} from "../../../../api/finance/refund";
import {searchJson} from "../../../../utils/dataStorage";
const {RangePicker} = DatePicker;

class MerchantRefund extends Component {
	constructor(props) {
		super(props);
		this.state = {
			api: refundRecords,
			data:[],
			paginationParams: {
				searchJson: searchJson({member_type: 'MERCHANT'})
			},
			searchJson:{
				member_info:'',
				trade_no:'',
				type:'',
				state:'',
				created_at:'',
			},
		};
		this.child = React.createRef();
	}
	
	// 
	paginationChange = (list) =>{
		this.setState({data:list})
	};
	
	// 
	search = () =>{
		let obj = {};
		let searchJsons = this.state.searchJson;
		for (let key in searchJsons){
			if(searchJsons[key]){
				obj[key] = searchJsons[key]
			}
		}
		obj['member_type'] = 'MERCHANT';
		this.setState({
			paginationParams:{...this.state.paginationParams,
				searchJson:searchJson(obj)}
		},()=>{
			this.child.current.pagination(this.child.current.state.current)
		});
	};
	
	// 
	onDateChange = (date,dateString) =>{
		this.setState({searchJson:{...this.state.searchJson,created_at:dateString}})
	};
	
	//
	changeSearchValue = (e,type) =>{
		this.setState({searchJson:{...this.state.searchJson,[type]:e.target.value}})
	};
	
	// 
	clear = () =>{
		let searchJson = {
			member_info:'',
			trade_no:'',
			type:'',
			state:'',
			created_at:'',
		};
		this.setState({searchJson},()=>{
			this.search()
		})
	};
	
	
	render() {
		
		const columns = [
			{
				title: '//',
				dataIndex: 'nickname',
			},
			{
				title: '',
				dataIndex: 'trade_no',
			},
			{
				title: '',
				dataIndex: 'type',
				render : (text,record) => {
					switch (text) {
						case 'ORDER_EXCEPTION':
							return '';
							break;
						case 'MEMBER_AFTER_SALE':
							return '';
							break;
						case 'ORDER_MANUAL_CANCEL':
							return '';
							break;
						case 'ORDER_PLATFORM_CANCEL':
							return '';
							break;
						case 'ORDER_AGENT_PRODUCT_EXCEPTION':
							return '';
							break;
					}
				}
			},
			{
				title: '',
				dataIndex: 'applied_at',
			},
			{
				title:'',
				dataIndex:'state_desc'
			},
			{
				title:'/',
				render: (text,record) => `${record.settlement_total_fee}/${record.refund_amount}`
			},
			{
				title:'',
				dataIndex:'remark'
			},
		];
		
		return (
			<Fragment>
				<div className="cr_chartContent">
					<ul className="filter">
						<li className="needMargin">
							
							<Input
								placeholder='/'
								value={this.state.searchJson['member_info']}
								onChange={(e)=>{this.changeSearchValue(e,'member_info')}}
							/>
						</li>
						<li className="needMargin">
							
							<Input
								placeholder=""
								value={this.state.searchJson['trade_no']}
								onChange={(e)=>{this.changeSearchValue(e,'trade_no')}}
							/>
						</li>
						<li className="needMargin">
							
							<ConfigProvider locale={zh_CN}>
								<RangePicker
									onChange={this.onDateChange}
								/>
							</ConfigProvider>
						
						</li>
						<li >
							
							<Select
								value={this.state.searchJson.type}
								onChange={(e)=>{
									this.setState({searchJson:{...this.state.searchJson,type:e}})
								}}
								defaultActiveFirstOption={false}
							>
								<Select.Option  value=""></Select.Option>
								<Select.Option  value="ORDER_EXCEPTION"></Select.Option>
								<Select.Option  value="MEMBER_AFTER_SALE"></Select.Option>
								<Select.Option  value="ORDER_MANUAL_CANCEL"></Select.Option>
								<Select.Option  value="ORDER_PLATFORM_CANCEL"></Select.Option>
								<Select.Option  value="ORDER_AGENT_PRODUCT_EXCEPTION"></Select.Option>
							</Select>
						</li>
						<li >
							
							<Select
								value={this.state.searchJson.state}
								onChange={(e)=>{
									this.setState({searchJson:{...this.state.searchJson,state:e}})
								}}
								defaultActiveFirstOption={false}
							>
								<Select.Option  value="0"></Select.Option>
								<Select.Option  value="1"></Select.Option>
								<Select.Option  value="2"></Select.Option>
								<Select.Option  value="3"></Select.Option>
							</Select>
						</li>
						<li className="button">
							<Button size="small" type="primary" onClick={this.search}></Button>
							{
								window.hasPermission("refund_detailed_export") && <Button
									size="small"
								
								>
								</Button>
							}
							<span className="clear" onClick={this.clear}></span>
						</li>
					</ul>
					<div className="chart u_chart">
						<Table
							columns={columns}
							rowKey={record => record.refund_id}
							pagination={false}
							rowClassName={(record, index) => {
								let className = '';
								if (index % 2 ) className = 'dark-row';
								return className;
							}}
							dataSource={this.state.data}
						/>
					</div>
					<div className="pagination">
						<CustomPagination
							api={this.state.api}
							ref={this.child}
							params={this.state.paginationParams}
							valChange={this.paginationChange}
						/>
					</div>
				</div>
			</Fragment>
		);
	}
}

export default withRouter(MerchantRefund);
