import React from 'react';
import {Button, Table, Modal, message} from 'antd'
import '../ShopManage/css/shopManage.sass'
import {withRouter} from 'react-router-dom'
import SearchInput from "../../../components/SearchInput/SearchInput";
import CustomItem from "../../../components/CustomItems/CustomItems";
import {user_values} from "../../../utils/user_fields";
import {searchJson} from "../../../utils/dataStorage";
import {getFrozen,unfrozen,deleteFrozen} from "../../../api/shops/frozen";
import CustomPagination from "../../../components/Layout/Pagination";
import AdvancedFilterComponent from "../../User/UserManage/AdvancedFilterComponent";
class FrozenShop extends React.Component{
	constructor(props){
		const columns = [
			{
				title: '',
				dataIndex: 'name',
				render: (text,record) => <span
					style={{'color':'#4F9863','cursor':'pointer'}}
					onClick={()=>this.jump(record)}>{text}</span>,
			},
			{
				title: '',
				dataIndex: 'keeper_name',
			},
			{
				title: '',
				dataIndex: 'code',
			},
			{
				title: '',
				dataIndex: 'total_sale',
			},
			{
				title: '',
				dataIndex: 'channel',
			},
			{
				title: '',
				dataIndex: 'status',
			},
			{
				title: '',
				render: (text,record) =>
					<div>
						{
							window.hasPermission("shop_frozen_unfreeze") && <span
								style={{'color':'#4F9863','cursor':'pointer'}}
								onClick={()=>this.unfreeze(record)}
							>
						</span>
						}
						
						{
							window.hasPermission("shop_frozen_delete") && <span
								style={{'color':'#4F9863','cursor':'pointer',marginLeft:'30px'}}
								onClick={()=>this.deleteShop(record)}
							>
						</span>
						}
						
						<span
							style={{'color':'#4F9863','cursor':'pointer',marginLeft:'30px'}}
						>
						</span>
					</div>
				,
			},
		];
		
		super(props);
		this.child = React.createRef();
		this.state = {
			api:getFrozen,
			id:'',
			filterVisible:false,
			customVisible:false,
			user_data:[],
			checkedAry:[],     // id
			paginationParams:{
				logic_conditions:[],
				search:''
			},
			columns:columns
		};
	}
	
	componentWillMount() {
		document.addEventListener('click', this.closeCustom);
	}
	
	refresh = ()=>{
		this.setState({
			filterVisible:false,
			paginationParams:{
				logic_conditions:[],
				search:''
			}
		},()=>{
			this.child.current.pagination(this.child.current.state.current)
		})
	};
	
	jump = (record) =>{
		if (window.hasPermission("shop_frozen_display")) {
			this.props.history.push({pathname:"/shops/shopDetails",state:{id:record.id}})
		} else {
			message.error('')
		}
		
	};
	
	//
	unfreeze = (record) =>{
		this.showConfirm('',record.id,unfrozen)
	};
	
	// 
	deleteShop = (record) =>{
		this.showConfirm('',record.id,deleteFrozen)
	};
	
	showConfirm = (msg,id,fn) =>{
		let refresh = this.refresh;
		let confirmModal = Modal.confirm({
			title: (
				<div className= 'u_confirm_header'>
					
					<i className="iconfont" style={{'cursor':'pointer'}} onClick={()=>{
						confirmModal.destroy()
					}}>&#xe82a;</i>
				</div>
			),
			icon:null,
			width:'280px',
			closable:true,
			centered:true,
			content: (
				<div className="U_confirm">
					{msg}
				</div>
			),
			cancelText: '',
			okText:'',
			okButtonProps: {
				size:'small'
			},
			cancelButtonProps:{
				size:'small',
			},
			onOk() {
				fn({},id).then(r=>{
					message.success(`${msg}!`);
					refresh()
				})
			}
		});
	};
	
	
	
	// 
	search = (value) =>{
		this.setState({
			api:getFrozen,
			paginationParams:{...this.state.paginationParams,
				searchJson:searchJson({search:value})}
		},()=>{
			this.child.current.pagination(this.child.current.state.current)
		});
	};
	//
	higherFilter = () =>{
		this.setState({filterVisible:true})
	};
	closeHigherFilter = () =>{
		this.setState({filterVisible:false})
	};
	onSubmit = (data) =>{
		this.setState({api:getFrozen,paginationParams:{...this.state.paginationParams,searchJson:searchJson({logic_conditions:data})}},()=>{
			this.child.current.pagination(this.child.current.state.current)
		});
	};
	
	
	
	//
	showCustom = (e) =>{
		e.nativeEvent.stopImmediatePropagation();
		this.setState({customVisible:true})
	};
	closeCustom = () =>{
		this.setState({customVisible:false})
	};
	
	
	handleCustom = (e) =>{
		let ary = [];
		e.forEach(e=>{
			user_values.forEach(u=>{
				u.children.forEach(c=>{
					if(e == c.value){
						let obj = {};
						obj.title = c.label;
						obj.dataIndex = e;
						ary.push(obj)
					}
				})
			})
		});
		ary[0].render = (text,record) => <span
			style={{'color':'#4F9863','cursor':'pointer'}}
			onClick={()=>this.jump(record)}>{text}</span>
		this.setState({columns:ary})
	};
	
	// 
	paginationChange = (list) =>{
		console.log(list);
		this.setState({user_data:list})
	};
	
	
	
	render(){
		const rowSelection = {
			onChange: (selectedRowKeys, selectedRows) => {
				this.setState({checkedAry:selectedRowKeys})
			},
			getCheckboxProps: record => ({
				disabled: record.name === 'Disabled User', // Column configuration not to be checked
				name: record.name,
			})
		};
		
		return (
			<div>
				<AdvancedFilterComponent
					visible={this.state.filterVisible}
					onCancel={this.closeHigherFilter}
					onSubmit={this.onSubmit}
					refresh={this.refresh}
					showAddGroup={this.showAddGroup}
					closeAddGroup={this.closeAddGroup}
					showAddTags={this.showAddTags}
					closeAddTags={this.closeAddTags}
				/>
				
				
				<div className="s_body frozen_body">
					<div className="headerLeft">
						<SearchInput
							getDatas={this.search}
							text=''
						/>
						<h4 className="higherFilter" onClick={this.higherFilter}></h4>
						{
							window.hasPermission("shop_frozen_export") && <Button size="small" disabled={this.state.checkedAry.length == 0}></Button>
						}
					</div>
					<Button type="primary" size="small" onClick={this.showCustom}></Button>
				</div>
				
				<div style={{'display':this.state.customVisible?'block':'none'}} className="custom"  onClick={this.showCustom}>
					<CustomItem data={user_values}  handleCustom={this.handleCustom} />
				</div>
				
				<div className="chart u_chart">
					<Table
						rowSelection={rowSelection}
						columns={this.state.columns}
						rowKey={record => record.id}
						pagination={false}
						rowClassName={(record, index) => {
							let className = '';
							if (index % 2 ) className = 'dark-row';
							return className;
						}}
						dataSource={this.state.user_data}
					/>
				</div>
				<div className="pagination">
					<CustomPagination
						api={this.state.api}
						ref={this.child}
						params={this.state.paginationParams}
						id={this.state.id}
						valChange={this.paginationChange}
					/>
				</div>
			
			
			
			
			</div>
		)
	}
}
export default withRouter(FrozenShop)
