import _ from 'lodash'
export const types = [
	{
		cn: '',
		en: 'MANUAL_RECEIVE'
	},
	{
		cn: '',
		en: 'PLATFORM_SEND'
	},
	{
		cn: '',
		en: 'INTEGRAL_EXCHANGE'
	},
	{
		cn: '',
		en: 'NEW_USER'
	}
];
export function transform(en) {
	let cn;
	_.map(types, type=>{
		if (en === type.en) {
			cn = type.cn
		}
	});
	return cn;
}
