import React, {Component} from 'react';
import {Table} from "antd";
import '../../../PrintSheet/index.sass'
import _ from 'lodash'

class PrintGrouponOrders extends Component {
	constructor(props) {
		super(props);
		this.state = {
			orders: [],
			data: []
		}
	}
	
	
	componentDidMount() {
		
		this.setState({
			orders: this.props.orders
		}, ()=>{
			window.print();
		});
	}
	render() {
		const columns = [
			{
				title: '',
				dataIndex: 'name',
				align: 'center'
			},
			// {
			// 	title: '',
			// 	dataIndex: 'product_spec_value',
			// 	align: 'center',
			// 	render: (text, record) => {
			// 		if (text === 'wholeRow') {
			// 			return ''
			// 		} else {
			// 			return text || '--'
			// 		}
			// 	}
			// },
			{
				title: '',
				dataIndex: 'unit',
				align: 'center',
				render: (text, record, index) => {
					if (record.name === '') {
						return '--';
					} else {
						return text ;  //++indexindex+1
					}
				}
			},
			{
				title: '',
				align: 'center',
				dataIndex: 'quantity',
			},
			{
				title: '()',
				dataIndex: 'price',
				align: 'center'
			},
			{
				title: '()',
				dataIndex: 'total_fee',
				align: 'center'
			},
		];
		const {orders} = this.state;
		return (
			<div id='printArea'>
				{
					orders.length ? orders.map((order,index) => {
						console.log(order);
						let totalQuantity = 0;
						let totalPrice = 0;
						let wholePrice = 0;
						order.group_products.length && order.group_products.map((item) => {
							totalQuantity += item.quantity;
						});
						totalPrice = order['total_fee'];
						wholePrice = order['settlement_total_fee'];
						const totalRow = {
							id: String(Math.random()),
							product_name: '',
							quantity: totalQuantity,  //
							total_fee: totalPrice, //
						};
						const wholeRow = {
							product_spec_value: 'wholeRow',
							product_name: '',
							quantity: '', //
						};
						order.items = [...order.group_products, totalRow, wholeRow];
						return <div className='printSheet' key={order.id}>
							<h4  className="shopInfo" >{order['initiator_name']}</h4>
							<h4>{order['initiator_keeper_name']}</h4>
							<h4>{order['initiator_keeper_mobile']}</h4>
							<h4>{order['trade_no']}</h4>
							<h4></h4>
							<h4></h4>
							<h4>{order['summary_date']}</h4>
							<h4>{order['shop_delivery_route'] && order['shop_delivery_route'].name}</h4>
							<div className="chart u_chart">
								<Table
									columns={columns}
									bordered={true}
									rowKey={record => record['item_id']}
									pagination={false}
									dataSource={order.items}
								/>
							</div>
						
						</div>
					}) : ''
				}
			</div>
		
		);
	}
}

export default PrintGrouponOrders;
