import React from 'react';
import {Button, message, Form} from "antd";
import {withRouter} from 'react-router-dom';
import {resetPwd} from "../../api/auth";
import './index.sass'
class ResetPassword extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			countDown:59,  //
			v_code:'',   //
			password_1:'',  // 
			password_2:'',  // 
		};
	}
	componentDidMount() {
		if(this.state.countDown < 60){
			this.sendSms()
		}
	}

	resetPassword = () =>{
		if(!this.state.v_code || !this.state.password_1 || !this.state.password_2){
			return
		}
		if(this.state.password_1 != this.state.password_2 ){
			return
		}
		resetPwd({}).then(r=>{

		})
	}
	
	
	sendSms = ()=>{
		window.timer = setInterval(()=>{
			this.setState({countDown:this.state.countDown-1});
			if(this.state.countDown === 0){
				clearInterval(window.timer);
				this.setState({countDown:60})
			}
		},1000)
	};
	
	render() {
		const reg = /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)[^]{8,16}$/;      //
		return (
			<div className="page-login">
				<div  className="loginBox">
					<Form>
						<div className="title">
							<h3></h3>
							<span>PineHub</span>
						</div>
						<div className="form">
							<div className="phone">
								:
								<h4>{this.props.location.state.phone}</h4>
							</div>
							<div className="user">
								<i className="iconfont">&#xe7ae;</i>
								<input
									type="tel"
									className="input"
									placeholder=""
									onInput={(e)=>{this.setState({v_code:e.target.value})}}
									onBlur={()=>{
										if (!this.state.v_code){
											message.error('');
										}
									}}
								/>
								<Button
									size="small"
									type="primary"
									disabled={this.state.countDown < 60}
									className="getCode"
									onClick={this.sendSms}
								>
									{this.state.countDown === 60?'' :this.state.countDown+'s'}
								</Button>
							</div>
							<div className="user">
								<i className="iconfont">&#xe7c9;</i>
								<input
									type="password"
									className="input"
									placeholder=""
									autoComplete="true"
									onInput={(e)=>{this.setState({password_1:e.target.value})}}
									onBlur={()=>{
										if(!this.state.password_1){
											message.error('');
										} else if (!reg.test(this.state.password_1)){
											message.error('8-20');
										}
									}}
								/>
							</div>
							<div className="user">
								<i className="iconfont">&#xe7c9;</i>
								<input
									type="password"
									className="input"
									autoComplete="true"
									placeholder=""
									onInput={(e)=>{this.setState({password_2:e.target.value})}}
									onBlur={()=>{
										if(this.state.password_2 != this.state.password_1){
											message.error('');
										}
									}}
								/>
							</div>
							<Button className="submit-btn" type="primary" size="small" onClick={this.resetPassword}>
								
							</Button>
						</div>
					</Form>
				</div>
				<div className="company">
					Copyright  2019 
				</div>
			</div>
		)
	}
}
export default withRouter(ResetPassword)