import React, {Component} from 'react';
import {Table} from "antd";
import _ from "lodash";

class CustomOrdersPrint extends Component {
	constructor(props) {
		super(props);
		this.state = {
			orders: [],
			data: []
		}
	}
	
	
	componentDidMount() {
		let o = this.props.orders;
		console.log(o, '====>');
		let ary = [];
		_.map(o, order=>{
			order.order.length && order.order.map(item=>{
				item.village = order.village;
				item.mobile = order.mobile;
				item.nameBig = order.name;
				item.address = order.address;
			});
			ary = ary.concat(order.order)
		});
		this.setState({
			orders: ary
		}, ()=>{
			window.print();
		});
	}
	
	
	render() {
		const columns = [
			{
				title: '',
				dataIndex: 'product_name',
				align: 'center'
			},
			{
				title: '()',
				dataIndex: 'unit',
				align: 'center',
				render: (text, record, index) => {
					if (record.name === '') {
						return '--';
					} else {
						return text ;  //++indexindex+1
					}
				}
			},
			{
				title: '',
				align: 'center',
				dataIndex: 'quantity',
			},
			{
				title: '()',
				dataIndex: 'price',
				align: 'center',
				render: (text,record) => {
					if (text) {
						return <span>{Number(text).toFixed(2)}</span>
					} else {
						return ''
					}
				}
			},
			{
				title: '()',
				dataIndex: 'total',
				align: 'center'
			},
		];
		const {orders} = this.state;
		console.log(orders, 'MMMMMMMMMMMMMMMMMMMMM');
		return (
			<div id='printArea'>
				{
					orders.length && orders.map((order) => {
						let totalQuantity = 0;
						let totalPrice = 0;
						let wholePrice = 0;
						order.products.length && order.products.map((item) => {
							totalQuantity += item.quantity;
							totalPrice += item.price;
							wholePrice += item.total;
						});
						const wholeRow = {
							unit: '',
							quantity: totalQuantity,
							price: '', //
							total: wholePrice.toFixed(2),
						};
						order.products = [...order.products, wholeRow];
						return <div className='printSheet' key={Math.random()}>
							<h4 className="shopInfo" >{order['village']}</h4>
							
							<h4>
								{order['nickname']}&nbsp;&nbsp;&nbsp;&nbsp;
								<span>
									{
										order['name']
									}
								</span>
							</h4>
							
							<h4>{order['trade_no'].slice(14,24)}</h4>
							<h4>
								{order['homeAddress']}
							</h4>
							<h4>
								{order['area']}
							</h4>
							<h4> {order['phone']}</h4>
							<h4>{order['paid_at']}</h4>
							<h4>{order['expect_receive_date']} {order['expect_receive_time_start']}-{order['expect_receive_time_end']}</h4>
							
							{
								order['shop_keeper_name'] ? <h4 >:{order['shop_keeper_name']}</h4> : ''
							}
							{
								order['shop_keeper_mobile'] ? <h4 >:{order['shop_keeper_mobile']}</h4> : ''
							}
							{
								order['shop_complete_address'] ? <h4 >:{order['shop_complete_address']}</h4> : ''
							}
							<h4>{order['nameBig']}</h4>
							<h4>{order['mobile']}</h4>
							<h4>{order['address']}</h4>
							<div className="chart u_chart">
								<Table
									columns={columns}
									bordered={true}
									rowKey={record => Math.random()}
									pagination={false}
									dataSource={order.products}
								/>
							</div>
						
						</div>
					})
				}
			</div>
		)
	}
}

export default CustomOrdersPrint;
