import React, {Component} from 'react';
import {Table} from "antd";
import _ from "lodash";
import {getNowDate} from "../../../../utils/dataStorage";


class CustomSummaryPrint extends Component {
	constructor(props) {
		super(props);
		this.state = {
			orders: [],
			data: []
		}
	}
	
	
	componentDidMount() {
		let o = this.props.orders;
		_.map(o, order=>{
			let allProducts = [];
			order.order.length && order.order.map(list => {
				list.products.length && list.products.map(item => {
					let index = _.findIndex(allProducts, product => {
						return product['product_name'] === item['product_name']
					});
					if (index > -1) {
						allProducts[index].quantity = allProducts[index].quantity + item.quantity
					} else {
						allProducts.push(item)
					}
				})
			});
			order.products = allProducts;
		});
		this.setState({
			orders: o
		}, ()=>{
			window.print();
		});
	}
	
	
	render() {
		const columns = [
			{
				title: '',
				dataIndex: 'product_name',
				align: 'center'
			},
			{
				title: '',
				dataIndex: 'unit',
				align: 'center',
				render: (text, record, index) => {
					if (record.name === '') {
						return '--';
					} else {
						return text ;  //++indexindex+1
					}
				}
			},
			{
				title: '',
				align: 'center',
				dataIndex: 'quantity',
			},
			{
				title: '()',
				dataIndex: 'price',
				align: 'center',
				render: (text,record) => {
					if (text) {
						return <span>{Number(text).toFixed(2)}</span>
					} else {
						return ''
					}
				}
			},
			{
				title: '()',
				dataIndex: 'total1',
				align: 'center'
			},
		];
		const {orders} = this.state;
		return (
			<div id='printArea'>
				{
					orders.length && orders.map((order,index) => {
						let totalQuantity = 0;
						let totalPrice = 0;
						order.products.length && order.products.map((item) => {
							totalQuantity += item.quantity;
						});
						totalPrice = order['total_fee'];
						const totalRow = {
							id: String(Math.random()),
							product_name: '',
							quantity: totalQuantity,  //
							total_fee: '', //
							price: ''
						};
						const wholeRow = {
							product_spec_value: 'wholeRow',
							product_name: '',
							quantity: '', //
							price: ''
						};
						order.products = [...order.products, totalRow, wholeRow];
						return <div className='printSheet' key={Math.random()}>
							<h4  className="shopInfo" >:{order['village']}</h4>
							<h4  >{order['address']}</h4>
							<h4>{order['name']}</h4>
							<h4 >:{order['mobile']}</h4>
							<h4></h4>
							<h4></h4>
							<h4></h4>
							<h4>{getNowDate()}</h4>
							<h4></h4>
							<div className="chart u_chart">
								<Table
									columns={columns}
									bordered={true}
									rowKey={record => Math.random()}
									pagination={false}
									dataSource={order.products}
								/>
							</div>
						
						</div>
					})
				}
			</div>
		);
	}
}

export default CustomSummaryPrint;
