import React from 'react';
import './css/ShopApplication.sass'
import {message, Modal, Radio} from "antd";
import {changeStatus} from "../../../api/shops/shopManage";

class ShopApplication extends React.Component{
	constructor(props) {
		super(props);
		this.state = {
			status:'',
			isShow: true
		};
	}
	
	componentWillReceiveProps(nextProps, nextContext) {
		if(!nextProps.selectedRows.length) return;
		let isShow = nextProps.selectedRows.find(item=>{
			return item.channel === ''
		});
		this.setState({isShow: !isShow})
	}
	
	
	handleCancel = ()=>{
		this.props.onClose()
	};
	
	confirm = () => {
		if (this.state.isShow) {
			this.handleSubmit()
		} else {
			this.showNotice()
		}
	};
	
	showNotice = () =>{
		let submit = this.handleSubmit;
		let confirmModal = Modal.confirm({
			title: (
				<div className= 'u_confirm_header'>
					
					<i className="iconfont" style={{'cursor':'pointer'}} onClick={()=>{
						confirmModal.destroy()
					}}>&#xe82a;</i>
				</div>
			),
			icon:null,
			width:'280px',
			closable:true,
			centered:true,
			content: (
				<div className="U_confirm">
					,
				</div>
			),
			cancelText: '',
			okText:'',
			okButtonProps: {
				size:'small'
			},
			cancelButtonProps:{
				size:'small',
			},
			onOk() {
				submit()
			}
		});
	};
	
	handleSubmit = () =>{
		changeStatus({
			status:this.state.status,
			shop_ids:this.props.checkedAry
		}).then(r=>{
			message.success('');
			this.props.refresh();
			this.handleCancel();
			this.setState({status: ''})
		})
	};
	
	render(){
		return (
			<div>
				<Modal
					title=""
					width={520}
					visible={this.props.visible}
					onCancel={this.handleCancel}
					maskClosable={false}
					okText=""
					cancelText=""
					onOk={this.confirm}
				>
					<div className="s_channel">
						<span className="left">:</span>
						<Radio.Group value={this.state.status} onChange={
							(e)=>{
								this.setState({status:e.target.value})
							}
						}>
							<Radio value={100} style={{display: this.state.isShow ? 'inline-block': 'none'}}></Radio>
							<Radio value={200} style={{display: this.state.isShow ? 'inline-block': 'none'}}></Radio>
							<Radio value={0}></Radio>
						</Radio.Group>
					</div>
				</Modal>
			</div>
		)
	}
}
export default ShopApplication
