// 

import React from 'react'
import {Modal, Input, Table} from 'antd';
import './css/common.sass'
import './css/staffList.sass'
import {admins} from "../../api/setting";
import {trim} from "../../utils/dataStorage";
import SearchInput from "../../components/SearchInput/SearchInput";
const Search = Input.Search;
const { Column } = Table;

class StaffFrozen  extends React.Component{
	constructor(props){
		super(props);
		this.state = {
			tableData:[],
			confirmLoading: false
		};
	}
	
	
	componentWillReceiveProps(nextProps, nextContext) {
		if(nextProps.visible){
			admins({limit:50,page:1,only_trashed:true}).then(r=>{
				this.setState({tableData:r.data})
			}).catch(_=>{})
		}
	}
	
	handleCancel = () => {
		this.props.onClose()
	};
	
	//
	searchDatas = (value) =>{
		admins({limit:10,page:1,only_trashed:true,search:`name:${value};mobile:${value}`}).then(r=>{
			this.setState({tableData:r.data})
		}).catch(_=>{});
	};
	
	render() {
		return (
			<div>
				<Modal
					title=""
					width={1000}
					centered={true}
					visible={this.props.visible}
					onCancel={this.handleCancel}
					footer={null}
				>
					<div className="list">
						
						<SearchInput
							getDatas={this.searchDatas}
							text=""
						/>
						<div className="listChart">
							<Table dataSource={this.state.tableData} rowKey={record => record.id}>
								<Column
									style={{width:'170px'}}
									title=""
									dataIndex="name"
									key="name"
								/>
								<Column
									style={{width:'150px'}}
									title=""
									dataIndex="mobile"
									key="mobile" />
								<Column
									style={{width:'190px'}}
									title=""
									dataIndex="created_at"
									key="created_at" />
								<Column
									style={{width:'190px'}}
									title=""
									dataIndex="deleted_at"
									key="deleted_at" />
								<Column
									style={{width:'150px'}}
									title=""
									render={(text,record)=>{
										let roles = [];
										if(record.roles){
											record.roles.forEach(item=>{
												roles.push(item.name)
											})
										}
										return (
											<span>
												{roles.toString()}
											</span>
										)
									}}

								/>
							</Table>
						</div>
					</div>
				</Modal>
			</div>
		);
	}
}
export default StaffFrozen