import React, {Component} from 'react';
import {Modal, Radio} from "antd";

class PreviewDetails extends Component {

    constructor(props) {
        super(props);
    }


    handleCancel = () => {
        this.props.onClose()
    };

    status = (status) => {
        let text = '';
        switch (status) {
            case 0:
                text = '';
                break;
            case 2:
                text = '';
                break;
            default:
                text = ''
        }
        return text
    };

    handleType = (status) => {
        let text = '';
        switch (status) {
            case 'DISCOUNT':
                text = '';
                break;
            case 'FULL_REDUCTION':
                text = '';
                break;
            default:
                text = ''
        }
        return text
    };

    handleTarget = (status) => {
        let text = '';
        switch (status) {
            case 'ALL_USER':
                text = '';
                break;
            case 'NEW_USER':
                text = '';
                break;
            default:
                text = ''
        }
        return text
    };


    render() {
        return (
            <div>
                <Modal
                    title=""
                    width={520}
                    visible={this.props.visible}
                    onCancel={this.handleCancel}
                    maskClosable={false}
                    footer={null}
                >
                    <ul className="mainUl couponDetail">
                        <li>
                            <span className="left">:</span>
                            <span className='limit'>{this.props.details.name}</span>
                        </li>
                        <li>
                            <span className="left">:</span>
                            <span className='limit'>{this.props.details.desc}</span>
                        </li>
                        <li>
                            <span className="left">:</span>
                            <span className='limit'>{this.handleType(this.props.details.type)}</span>
                        </li>
                        {
                            this.props.details['applicable_desc'] && this.props.details['applicable_desc'].length ?  <li>
                                <span className="left">:</span>
                                <span className='limit'>{this.props.details['applicable_desc'][0]['value_display']}</span>
                            </li> : ''
                        }

                        <li>
                            <span className="left">:</span>
                            <img style={{width: '100px', height: '100px'}} src={this.props.details.image} alt=""/>
                        </li>
                        {/*<li>*/}
                        {/*    <span className="left">:</span>*/}
                        {/*    <span className='limit'>{this.props.details['buy_max_num']}</span>*/}
                        {/*</li>*/}
                        <li>
                            <span className="left">:</span>
                            <span className='limit'>{this.props.details['user_limit_day']}</span>
                        </li>
                        <li>
                            <span className="left">:</span>
                            <span className='limit'>{this.props.details['user_limit_num']}</span>
                        </li>
                        <li>
                            <span></span>
                            <span className='limit'>{this.props.details['is_join_sales_cashback'] ? '' : ''}</span>
                        </li>
                        {
                            this.props.details.type === 'NONE' && <li>
                                <span className="left">:</span>
                                <span className='limit'>{this.props.details['max_discount_amount']}</span>
                            </li>
                        }
                        {
                            this.props.details.type === 'DISCOUNT' && <li>
                                <span className="left">(0-100):</span>
                                <span className='limit'>{this.props.details.discount}</span>
                            </li>
                        }
                        {
                            this.props.details.type === 'FULL_REDUCTION' && <li>
                                <span className="left">:</span>
                                <span className='limit'>{this.props.details['limit_amount']}{this.props.details['discount_amount']}</span>
                            </li>
                        }
                        <li>
                            <span className="left">:</span>
                            <span className='limit'>{this.props.details['start_date'] || ''}</span>
                        </li>
                        <li>
                            <span className="left">:</span>
                            <span className='limit'>{this.props.details['end_date'] || ''}</span>
                        </li>
                        <li>
                            <span className="left">:</span>
                            <span className='limit'>{this.status(this.props.details.status)}</span>
                        </li>
                    </ul>
                </Modal>
            </div>
        );
    }
}

export default PreviewDetails;