import React, {Component,Fragment} from 'react';
import {ConfigProvider, Modal, Pagination, Table} from "antd";
import '../../../../style/pagination.sass'
import {receiveCoupons} from "../../../../api/marketing/coupon";
import zhCN from "antd/lib/locale-provider/zh_CN";

class PickUpDetails extends Component {
	constructor(props) {
		super(props);
		this.state = {
			data:[],
			id:'',
			total: 0,
			current:1,
		};
	}
	
	componentWillReceiveProps(nextProps, nextContext) {
		console.log(nextProps);
		if(!nextProps.couponId) return;
		if (nextProps.couponId === this.props.couponId) return;
		this.setState({id:nextProps.couponId});
		this.pagination(1,nextProps.couponId)
	}
	
	pagination = (page,id) =>{
		console.log(page, '....');
		let params = {};
		params.limit = 10;
		params.page = page;
		receiveCoupons(params,id).then(r=>{
			this.setState({data:r.data,total:r.meta.pagination.total});
		})
	};
	
	showTotal = (total,range) =>{
		return ` ${total}${range[0]}-${range[1]} `
	};
	
	onChange = page => {
		this.pagination(page,this.state.id);
		this.setState({
			current: page,
		});
	};
	
	handleCancel = () =>{
		this.props.onCancel()
	};
	
	render() {
		
		const columns = [
			{
				title: '',
				dataIndex: 'coupon_name',
			},
			{
				title: '',
				dataIndex: 'benefit',
			},
			// {
			// 	title: '',
			// 	dataIndex: 'spec' || '',
			// },
			{
				title: '',
				dataIndex: 'received_at',
			},
			{
				title: '',
				dataIndex: 'used_at',
			},
			{
				title: '',
				dataIndex: 'receiver_name',
			},
			{
				title: '',
				dataIndex: 'trade_no' || '',
			},
			{
				title: '',
				dataIndex: 'state_desc',
			},
		];
		
		return (
			<Fragment>
				<Modal
					title=""
					width={1000}
					visible={this.props.visible}
					onCancel={this.handleCancel}
					maskClosable={false}
					footer={null}
				>
					<div className="chart" id="s_chart">
						<Table
							columns={columns}
							rowKey={record => record.id}
							pagination={false}
							dataSource={this.state.data}
						/>
					</div>
					<div className="pagination">
						<ConfigProvider locale={zhCN}>
							<Pagination
								total={this.state.total}
								showTotal={this.showTotal}
								showQuickJumper
								defaultCurrent={1}
								pageSize={10}
								onChange={this.onChange}
							/>
						</ConfigProvider>
					</div>
				</Modal>
			</Fragment>
		);
	}
}

export default PickUpDetails;
