import React, {Component, Fragment} from 'react';
import '../css/customMessage.sass'
import {withRouter} from 'react-router-dom'
import {Collapse, Icon, Input , Button ,message} from 'antd';
import ReactColor from "../../../../components/ReactColor/ReactColor";
import {customWeChatMessage,weChatMessageList,deleteChatMessageList} from "../../../../api/marketing/message";
import IconFont from "../../../../utils/IconFont";

const { Panel } = Collapse;
class CustomWeChatMessage extends Component {
	constructor(props) {
		super(props);
		this.child = React.createRef();
		this.state = {
			item: props.location.state.item,
			data:[],
		}
	}
	
	componentDidMount() {
		this.refresh();
	}
	
	refresh = () =>{
		weChatMessageList({limit:100,page:1},this.props.location.state.item.id).then(r=>{
			this.setState({data:r.data})
		}).catch(_=>{})
	};
	
	submit = () =>{
		let items = this.state.item.items;
		let content = [];
		items.forEach(item=>{
			let data = {};
			data.value = item.value;
			data.color = item.color || '#666';
			content.push(data)
		});
		customWeChatMessage({content},this.state.item.id).then(r=>{
			message.success(r.message);
			this.refresh()
		}).catch(_=>{})
	};
	
	valueChange = (v,e) =>{
		let item = this.state.item;
		item.items.forEach(i=>{
			if(i.key === v.key){
				i.value = e.target.value
			}
		});
		this.setState({item})
	};
	
	colorChange = (v,c='#000') =>{
		let item = this.state.item;
		item.items.forEach(i=>{
			if(i.key === v.key){
				i.color = c
			}
		});
		this.setState({item})
	};
	
	editMessage = (item) =>{
		this.props.history.push({pathname:'/marketing/editWeChatMessage',state:{item,listId:this.props.location.state.item.id}})
	};
	
	// 
	show = (item) => {
		let data = this.state.data;
		data.forEach(i=>{
			if(i.id === item.id){
				i.showDelete = true
			}
		});
		this.setState({data})
	};
	
	//  
	hide = (item) => {
		let data = this.state.data;
		data.forEach(i=>{
			if(i.id === item.id){
				i.showDelete = false
			}
		});
		this.setState({data})
	};
	
	// 
	delete = (item) =>{
		deleteChatMessageList({},this.props.location.state.item.id,item.id).then(r=>{
			message.success(r.message);
			this.refresh();
		}).catch(_=>{})
	};
	
	render() {
		const {item,data} = this.state;
		return (
			<Fragment>
				<div className="newBox">
					<div className="preview">
						<h3></h3>
						<div className="m_ul">
							<h4>{item.title}</h4>
							<ul>
								{
									item.items.map(i=>(<li key={i.key}>
										<span className="key">{i.key}</span>
										<span className="value" style={{color:i.color || '#666'}}>{i.value}</span>
									</li>))
								}
							</ul>
							<p>
								<span></span>
								<Icon type="right" />
							</p>
						</div>
					</div>
					<div className="configuration">
						<h2></h2>
						<ul>
							{
								item.items.map(i=>(<li key={i.key}>
									<span>{i.key}</span>
									<Input value={i.value} onChange={e=>this.valueChange(i,e)} />
									<ReactColor ref={this.child} colorChange={(color)=>this.colorChange(i,color)} />
								</li>))
							}
						</ul>
						<div className="btnBox">
							<Button size="small" type="primary" onClick={this.submit}></Button>
						</div>
					
					</div>
					<div className="information">
						<h3>()</h3>
						<span className='span'>
						xxxxxx
					</span>
						<span>
						
					</span>
						<Collapse bordered={false} >
							<Panel header="" key="1">
								<p>title: </p>
								<p>validateTime: </p>
								<p>cardCode: code</p>
							</Panel>
							<Panel header="" key="2">
								<p>username: </p>
								<p>name: username</p>
								<p>selfPickUpCode: </p>
								<p>address: </p>
								<p>pickUpTime: </p>
								<p>title: </p>
								<p>amount: </p>
								<p>paidAt: </p>
							</Panel>
						</Collapse>
					</div>
				</div>
				<div className="m_list">
					{
						data.map(item=>(
							<div className="m_list_item">
								<div className="m_ul"
									 onMouseEnter={()=>this.show(item)}
									 onMouseLeave={()=>this.hide(item)}
								>
									<h4>{item.title}</h4>
									<ul onClick={()=>this.editMessage(item)}>
										{
											item.items.map((i)=>(<li key={i.key}>
												<span className="key">{i.key}</span>
												<span className="value" style={{color:i.color || '#666'}}>{i.value}</span>
											</li>))
										}
									</ul>
									<p onClick={()=>this.editMessage(item)}>
										<span></span>
										<Icon type="right" />
									</p>
									<div className="delete" style={{visibility:item.showDelete?'visible':'hidden'}} onClick={()=>this.delete(item)}>
										<IconFont type="icon-delete-fill"  />
									</div>
								</div>
							</div>
						))
					}
				</div>
			</Fragment>
			
		);
	}
}

export default withRouter(CustomWeChatMessage);
