import React, {Fragment} from "react";
import {Button} from "antd";
import './css/orderDetail.sass'
import {orderDetail} from "../../../api/order/orderManage";

export default class OrderDetail extends React.Component{
	constructor(props) {
		super(props);
		this.state = {
			details: {}
		}
	}
	
	componentDidMount() {
		let id = this.props.location.state.id;
		orderDetail({},id).then(r=>{
			this.setState({details: r.data})
		}).catch(_=>{})
	}
	
	goBack = () => {
		this.props.history.push({pathname: this.props.location.state.path,state:{current: this.props.location.state.current}})
	};
	
	render() {
		const {details} = this.state;
		return (
			<div className="orderDetail">
				<div className="u_top">
					<div className="u_header">
						<span></span>
						<Button type="default" size="small" onClick={this.goBack}></Button>
					</div>
					<div className="u_body_one">
						<ul className="u_body_top">
							<li >
								<p>{details['trade_no']}</p>
								<p>{details['transaction_id']}</p>
								<p>{details['order_type']}</p>
								<p>{details['delivery_type_desc']}</p>
								<p>{details['introducer_code']}</p>
							</li>
							{/*<li className="steps">*/}
							{/*	<Steps size="small" current={1} labelPlacement="vertical">*/}
							{/*		<Step title="" description="2019-5-20 10:00"  />*/}
							{/*		<Step title="" description="2019-5-20 10:00"  />*/}
							{/*	</Steps>*/}
							{/*</li>*/}
							<li>
								<h3></h3>
								{
									details['shop_info'] && <Fragment>
										<p>{details['shop_info'].name}</p>
										<p>{details['shop_info'].code}</p>
										<p>{details['shop_info']['keeper_name']}</p>
										<p>{details['shop_info']['keeper_mobile']}</p>
									</Fragment>
								}
							</li>
							<li>
								<h3></h3>
								{details['shipping_info'] && <Fragment>
									<p>{details['shipping_info']['consignee_name']}</p>
									<p>{details['shipping_info']['consignee_mobile_phone']}</p>
									<p>
										
										{details['shipping_info']['province']}
										{details['shipping_info']['city']}
										{details['shipping_info']['area']}
										{details['shipping_info']['detail_address']}
									</p>
								</Fragment>}
							</li>
						</ul>
					</div>
				</div>
				<ul className="u_body_four">
					<li>
						<div className="u_body_four_header">
							
						</div>
						<ul className="u_body_four_body">
							<li>
								{details['user_name']}
							</li>
							<li>
								{details['user_mobile']}
							</li>
						</ul>
					</li>
					<li>
						<div className="u_body_four_header">
							
						</div>
						<ul className="u_body_four_body center_order ">
							<li>
								{details['payment_type']}
							</li>
							<li>
								{details['settlement_total_fee']+ ''}
							</li>
							<li>
								{details['preferential_total_fee']+ ''}
							</li>
							<li>
								{details['refund_fee'] + ''}
							</li>
							<li>
								{details['refund_type']}
							</li>
							<li>
								: {details['cost_amount'] + ''}
							</li>
							<li>
								: {details['sales_amount'] + ''}
							</li>
						</ul>
					</li>
					<li>
						<div className="u_body_four_header">
							
						</div>
						{
							details.items && details.items.length ? <ul className="u_body_four_body">
								{
									details.items.map(item=>(
										<li>
											{item.name} X {item.quantity}
										</li>
									))
								}
							</ul>: ''
						}
					</li>
				</ul>
			</div>
		)
	}
	
	
}
