import React, {Component} from 'react';
import {Button, message, Modal, Select, Table,Switch} from "antd";
import BannerSetting from "./Modal/BannerSetting";
import {banners, deleteBanner,enableBanner,disableBanner} from "../../../api/marketing/banners";
import './css/index.sass'
import {searchJson} from "../../../utils/dataStorage";
class Banners extends Component {
	constructor(props) {
		super(props);
		this.state = {
			banners: [],
			settingVisible: false,
			banner: {},
			state: null,
			scene: null
		};
		this.child = React.createRef();
	}
	
	componentDidMount() {
		this.refresh();
	};
	
	refresh = () => {
		banners({}).then(r=>{
			this.setState({banners: r.data})
		}).catch(_=>{})
	};
	
	paginationChange = (list)=>{
		this.setState({banners:list})
	};
	
	createNewBanner = () =>{
		this.setState({settingVisible: true,banner: {}})
	};
	
	editBanner = (record) =>{
		this.setState({settingVisible: true,banner: record})
	};
	
	closeModal = () =>{
		this.setState({settingVisible: false})
	};
	
	
	deleteBanner = id => {
		let refresh = this.refresh;
		let confirmModal = Modal.confirm({
			title: (
				<div className= 'u_confirm_header'>
					
					<i className="iconfont" style={{'cursor':'pointer'}} onClick={()=>{
						confirmModal.destroy()
					}}>&#xe82a;</i>
				</div>
			),
			icon:null,
			width:'280px',
			closable:true,
			centered:true,
			content: (
				<div className="U_confirm">
					banner
				</div>
			),
			cancelText: '',
			okText:'',
			okButtonProps: {
				size:'small'
			},
			cancelButtonProps:{
				size:'small'
			},
			onOk() {
				// 
				deleteBanner({},id).then(r=>{
					message.success(r.message);
					refresh()
				}).catch(_=>{})
			}
		});
	};
	
	handleChange = (type,e) => {
		this.setState({[type]: e}, ()=>{
			console.log(e);
			console.log(this.state, '...');
		})
	};
	
	search = () => {
		let {scene,state} = this.state;
		let params = {};
		if (!state && state !== false) {
			if (scene) {
				params = {
					scene
				}
			} else {
				params = {}
			}
		} else {
			if (scene) {
				params = {
					scene,
					is_active: state
				}
			} else {
				params = {
					is_active: state
				}
			}
			
		}
		banners({searchJson: searchJson(params)}).then(r=>{
			this.setState({banners: r.data})
		}).catch(_=>{})
	};
	
	// 
	onSwitchChange = (e, record) => {
		console.log(e);
		console.log(record);
		let api = e ? enableBanner : disableBanner;
		let text = e ? '' : '';
		api({},record.id).then(r=>{
			message.success(`${text}Banner`);
			this.search()
		}).catch(_=>{})
	};
	
	
	render() {
		const columns = [
			{
				title: '',
				dataIndex: 'title',
			},
			{
				title: '',
				dataIndex: 'scene_desc',
			},
			{
				title: '',
				dataIndex: 'synopsis',
			},
			{
				title: '',
				dataIndex: 'image',
				render: (text, record) => (
					<img src={text} className='tableImage' alt=""/>
				)
			},
			{
				title: '',
				dataIndex: 'can_jump',
				render: (text,record) => (
					<span>{text ? '' : ''}</span>
				)
			},
			{
				title: '',
				dataIndex: 'action_type',
				render: (text,record)=>{
					if (record['can_jump']) {
						return text === 'PRODUCT_DETAIL'? '' : ''
					} else {
						return ''
					}
				}
			},
			{
				title: '',
				dataIndex: 'action_link',
				render: (text,record)=>{
					return record['can_jump'] ? text : ''
				}
			},
			{
				title: '',
				dataIndex: 'sort',
			},
			{
				title: '',
				dataIndex: 'is_active',
				render: (text,record) => (
					<span>{text ? '' : ''}</span>
				)
			},
			{
				title: '',
				render: (text,record) => (
					<Switch checked={record['is_active']} onChange={(e)=>this.onSwitchChange(e,record)} />
				)
			},
			{
				title: '',
				render: (text,record) =>
					<div>
						<span
							style={{'color':'#4F9863','cursor':'pointer','marginRight' : '10px'}}
							onClick={()=>this.editBanner(record)}
						>
						</span>
						{
							!record['is_active'] && <span
								style={{'color':'#4F9863','cursor':'pointer'}}
								onClick={()=>this.deleteBanner(record.id)}
							>
						</span>
						}
					</div>
			},
		];
		
		const scene = [
			{name: '',value: 'GENERAL_INDEX'},
			{name: '',value: 'WECHAT_MINIPROGRAM_INDEX'},
			{name: 'APP',value: 'APP_INDEX_MERCHANT'},
			{name: 'APP',value: 'APP_INDEX_DISTRIBUTOR'}
		];
		
		const state = [
			{name: '', value: true},
			{name: '', value: false}
		];
		
		const bannerSettingProps = {
			visible: this.state.settingVisible,
			banner: this.state.banner,
			onclose : this.closeModal,
			actId: this.state.actId,
			refresh: this.refresh
		};
		
		return (
			<div className='bannerSetting'>
				<BannerSetting {...bannerSettingProps} />
				
				<div className="chart">
					<Button className="addNew" onClick={this.createNewBanner}>
						<i className="iconfont">&#xe7e0;</i>
						Banner</Button>
					
					<div className="searchSelect">
						<Select
							defaultActiveFirstOption={false}
							value={this.state.scene}
							onChange={(e)=>this.handleChange('scene',e)}
							optionLabelProp="label"
							allowClear
							placeholder=''
						>
							{scene.map(item => (
								<Select.Option key={item.value} label={item.name}  value={item.value}>
									{item.name}
								</Select.Option>
							))}
						</Select>
						<Select
							defaultActiveFirstOption={false}
							value={this.state.state}
							onChange={(e)=>this.handleChange('state',e)}
							optionLabelProp="label"
							allowClear
							placeholder=''
						>
							{state.map(item => (
								<Select.Option  key={item.value} label={item.name}  value={item.value}>
									{item.name}
								</Select.Option>
							))}
						</Select>
						<span style={{color: '#4f9863', fontSize: '12px', cursor: 'pointer'}} onClick={this.search}></span>
					</div>
					
					<Table
						dataSource={this.state.banners}
						rowKey={record => record.id}
						pagination={false}
						columns={columns}
						rowClassName={(record, index) => {
							let className = '';
							if (index % 2 ) className = 'dark-row';
							return className;
						}}
					>
					
					</Table>
				</div>
			</div>
		);
	}
}

export default Banners;
