import React, {Component} from 'react';
import {Button, DatePicker, Input, ConfigProvider, Select, Table} from "antd";
import zh_CN from "antd/lib/locale-provider/zh_CN";
import CustomPagination from "../../../../components/Layout/Pagination";
import {userBalanceRecord} from "../../../../api/finance/balance";
import {searchJson} from "../../../../utils/dataStorage";
import '../css/shop.sass'
const {RangePicker} = DatePicker;

class ConsumerBalance extends Component {
	constructor(props) {
		super(props);
		this.state = {
			data:[],
			api:userBalanceRecord,
			searchJson:{
				type:'',
				'user.nickname':'',
				'user.real_name':'',
				'user.mobile':'',
				created_at:'',
			},
			paginationParams:{
			},
		};
		this.child = React.createRef();
	};
	
	
	// 
	search = () =>{
		let obj = {};
		let searchJsons = this.state.searchJson;
		for (let key in searchJsons){
			if(searchJsons[key]){
				obj[key] = searchJsons[key]
			}
		}
		this.setState({
			paginationParams:{...this.state.paginationParams,
				searchJson:searchJson(obj)}
		},()=>{
			this.child.current.pagination(this.child.current.state.current)
		});
	};
	
	// 
	onDateChange = (date,dateString) =>{
		this.setState({searchJson:{...this.state.searchJson,created_at:dateString}})
	};
	
	
	// 
	paginationChange = (list) =>{
		this.setState({data:list})
	};
	
	//
	changeSearchValue = (e,type) =>{
		this.setState({searchJson:{...this.state.searchJson,[type]:e.target.value}})
	};
	
	// 
	clear = () =>{
		let searchJson = {
			type:'',
			'user.nickname':'',
			'user.real_name':'',
			'user.mobile':'',
			created_at:'',
		};
		this.setState({searchJson},()=>{
			this.search()
		})
	};
	
	// 
	renderType = type =>{
		let text ;
		switch (type) {
			case 'DEPOSIT':
				text = '';
				break;
			case 'RECEIVE_RED_PACKET':
				text = '';
				break;
			case 'REFUND':
				text = '';
				break;
			case 'WITHDRAW':
				text = '';
				break;
			case 'CONSUME':
				text = '';
				break;
			default :
				text = ''
		}
		return text;
	};
	
	render() {
		const columns = [
			{
				title: '',
				dataIndex: 'nickname',
			},
			{
				title: '',
				dataIndex: 'income',
			},
			{
				title: '',
				dataIndex: 'expense',
			},
			{
				title: '',
				dataIndex: 'created_at',
			},
			{
				title:'',
				dataIndex:'type',
				render:(text,record) =>(this.renderType(text))
			},
			{
				title:'',
				dataIndex:'remark'
			},
		];
		
		return (
			<div className="overviewShopBalance">
				<p>:</p>
				<div className="ob_chartContent">
					<ul className="filter">
						<li className="needMargin">
							/
							<Input
								placeholder='/'
								value={this.state.searchJson['user.nickname']}
								onChange={(e)=>{this.changeSearchValue(e,'user.nickname')}}
							/>
						</li>
						<li className="needMargin">
							
							<Input
								placeholder=""
								value={this.state.searchJson['user.mobile']}
								onChange={(e)=>{this.changeSearchValue(e,'user.mobile')}}
							/>
						</li>
						<li className="needMargin">
							
							<ConfigProvider locale={zh_CN}>
								<RangePicker
									onChange={this.onDateChange}
								/>
							</ConfigProvider>
						</li>
						<li >
							
							<Select
								value={this.state.searchJson.type}
								onChange={(e)=>{
									this.setState({searchJson:{...this.state.searchJson,type:e}})
								}}
								defaultActiveFirstOption={false}
							>
								<Select.Option  value=""></Select.Option>
								<Select.Option  value="DEPOSIT"></Select.Option>
								<Select.Option  value="RECEIVE_RED_PACKET"></Select.Option>
								<Select.Option  value="REFUND"></Select.Option>
								<Select.Option  value="WITHDRAW"></Select.Option>
								<Select.Option  value="CONSUME"></Select.Option>
							</Select>
						</li>
						<li className="button">
							<Button size="small" type="primary" onClick={this.search}></Button>
							{
								window.hasPermission("balance_detailed_export") && <Button
									size="small"
								
								>
								</Button>
							}
						
							<span className="clear" onClick={this.clear}></span>
						</li>
					</ul>
					<div className="chart u_chart">
						<Table
							columns={columns}
							rowKey={record => record.id}
							pagination={false}
							rowClassName={(record, index) => {
								let className = '';
								if (index % 2 ) className = 'dark-row';
								return className;
							}}
							dataSource={this.state.data}
						/>
					</div>
					<div className="pagination">
						<CustomPagination
							api={this.state.api}
							ref={this.child}
							params={this.state.paginationParams}
							valChange={this.paginationChange}
							text={''}
						/>
					</div>
				</div>
			</div>
		);
	}
}

export default ConsumerBalance;
