import React, {Component} from 'react';
import {message, Modal, Table} from "antd";
import CustomPagination from "../../../../components/Layout/Pagination";
import {exchangeCodes, stopCode} from "../../../../api/marketing/cards";
import {searchJson} from "../../../../utils/dataStorage";
import '../css/details.sass'
let logic_conditions = {
    conditions: [
        {
            key: 'consume_card_exchange_code_state',
            operation: '=',
            value: 0
        }
    ],
    logic: 'and'
};
class RechargeDetails extends Component {
    constructor() {
        super();

        this.state = {
            api: exchangeCodes,
            activeTab: 0,
            paginationParams:{
                logic_conditions:[],
                search:'',
                searchJson: searchJson({logic_conditions: logic_conditions})
            },
            current: 1,
            id: null,
            data: []
        };
        this.child = React.createRef()
    };

    componentDidMount() {
        this.setState({id: this.props.location.state.id})
    }

    refresh = (key)=>{
        let logic_conditions = {
            conditions: [
                {
                    key: 'consume_card_exchange_code_state',
                    operation: '=',
                    value: key
                }
            ],
            logic: 'and'
        };
        this.setState({
            filterVisible:false,
            paginationParams:{
                search:'',
                searchJson:searchJson({logic_conditions})
            }
        },()=>{
            this.child.current.pagination(this.child.current.state.current)
        })
    };

    // 
    stopExchange = (id) => {
        let refresh = this.refresh;
        let {activeTab} = this.state;
        let confirmModal = Modal.confirm({
            title: (
                <div className= 'u_confirm_header'>
                    
                    <i className="iconfont" style={{'cursor':'pointer'}} onClick={()=>{
                        confirmModal.destroy()
                    }}>&#xe82a;</i>
                </div>
            ),
            icon:null,
            width:'280px',
            closable:true,
            centered:true,
            content: (
                <div className="U_confirm">
                    
                </div>
            ),
            cancelText: '',
            okText:'',
            okButtonProps: {
                size:'small'
            },
            cancelButtonProps:{
                size:'small'
            },
            onOk() {
                // 
                stopCode({},id).then(r=>{
                    message.success(r.message);
                    refresh(activeTab)
                })
            }
        });
    };

    // 
    paginationChange = (list) =>{
        this.setState({data:list})
    };

    // tab
    onChangeTab = item =>{
        this.setState({activeTab:item.key});
        this.refresh(item.key)
    };



    render() {
        const columns = [
            {
                title: '',
                dataIndex: 'exchange_code',
            },
            {
                title: '',
                dataIndex: 'user_nickname',
                render: (text, record) => (
                    <span>{text || ''}</span>
                )
            },
            {
                title: '',
                dataIndex: 'user_mobile',
                render: (text, record) => (
                    <span>{text || ''}</span>
                )
            },
            {
                title: '',
                dataIndex: 'exchange_time',
                render: (text, record) => (
                    <span>{text || ''}</span>
                )
            },
            {
                title: '',
                dataIndex: 'state',
                render: (text, record) => {
                    switch (text) {
                        case 0:
                            return '';
                        case 1:
                            return '';
                        case 2:
                            return '';
                        default:
                            return ''
                    }
                }
            },
            {
                title: '',
                render: (text,record) =>
                    <div>
                        {
                            record.state === 0 && <span
                                style={{'color':'#4F9863','cursor':'pointer','marginRight' : '10px'}}
                                onClick={()=>this.stopExchange(record.id)}
                            >
						</span>
                        }
                    </div>
            },
        ];
        const tabs = [
            {name:'',key:0},
            {name:'',key:1},
            {name:'',key:2},
            {name:'',key:3}
        ];

        return (
            <div className='cardDetails'>
                <div className="tabs">
                    <ul className="left">
                        {
                            tabs.map((item,index)=>{
                                return <li
                                    key={index}
                                    className={this.state.activeTab == item.key?'active':''}
                                    onClick={()=>this.onChangeTab(item)}
                                >{item.name}</li>
                            })
                        }
                    </ul>
                </div>
                <div className="chart">
                    <Table
                        dataSource={this.state.data}
                        rowKey={record => record.id}
                        pagination={false}
                        columns={columns}
                        rowClassName={(record, index) => {
                            let className = '';
                            if (index % 2 ) className = 'dark-row';
                            return className;
                        }}
                    >

                    </Table>
                </div>
                <div className="pagination">
                    <CustomPagination
                        api={this.state.api}
                        text=""
                        id={this.state.id}
                        ref={this.child}
                        params={this.state.paginationParams}
                        current={this.state.current}
                        valChange={this.paginationChange}
                    />
                </div>
            </div>
        );
    }
}

export default RechargeDetails;