import React, {Component} from 'react';
import {Button, Input, message, Modal, Select} from "antd";
import 'moment/locale/zh-cn';
import '../css/newSmsModule.sass'
import {createSMS,exportSmsTemplates} from "../../../../api/marketing/message";

const {Option} = Select;
const {TextArea} = Input;
class NewModule extends Component {
	state = {
		obj_type: 'USER',
		biz_type:0,
		name:'',
		content:'',
		remark:'',
		is_auto_send:0,
		code: ''
	};
	
	handleCancel = () =>{
		this.props.onCancel();
	};
	
	submit = () =>{
		console.log(this.props.mode);
		let data = this.state;
		if (this.props.mode === 'export') {
			if(!data.code){
				message.error('code');
				return
			}
		}
		if(!data.name){
			message.error('');
			return
		}
		if(!data.content){
			message.error('');
			return
		}
		if(!data.remark){
			message.error('');
			return
		}
		this.create(data)
	};
	
	create = data =>{
		let api = this.props.mode === 'export' ? exportSmsTemplates : createSMS;
		api({...data}).then(r=>{
			message.success(r.message);
			this.handleCancel();
			this.props.refresh();
		}).catch(_=>{})
	};
	
	typeChange = (type,e) =>{ this.setState({[type]:e}) };
	
	render() {
		return (
			<Modal
				title=""
				width={520}
				centered={true}
				visible={this.props.visible}
				onCancel={this.handleCancel}
				footer={null}
				maskClosable={false}
			>
				<ul className="mainUl">
					<li>
						<span className="left">:</span>
						<Select
							value={this.state.obj_type}
							onChange={(e)=>this.typeChange('obj_type',e)}
							className='sms_select'>
							<Option value="USER"></Option>
							<Option value="MERCHANT"></Option>
						</Select>
					</li>
					<li>
						<span className="left">:</span>
						<Select
							value={this.state.biz_type}
							onChange={(e)=>this.typeChange('biz_type',e)}
							className='sms_select'>
							<Option value={0}></Option>
							<Option value={1}></Option>
							<Option value={2}></Option>
							<Option value={3}>/</Option>
						</Select>
					</li>
					<li >
						<span className="left">:</span>
						<Input
							className="liInput"
							value={this.state.name}
							maxLength={30}
							onChange={(e)=>{
								this.setState({name:e.target.value})
							}}
							placeholder=' 30'
						/>
					</li>
					<li>
						<span className="left">:</span>
						<TextArea
							className="liTextArea"
							rows={3}
							value={this.state.content}
							onChange={(e)=>{
								this.setState({content:e.target.value})
							}}
							placeholder=":${code};&#13;&#10;::${code},5"
						/>
					</li>
					{
						this.props.mode === 'export' && <li>
							<span className="left">code:</span>
							<Input
								className="liTextArea"
								value={this.state.code}
								onChange={(e)=>{
									this.setState({code:e.target.value})
								}}
								placeholder="code"
							/>
						</li>
					}
					<li>
						<span className="left">:</span>
						<TextArea
							className="liTextArea"
							rows={3}
							value={this.state.remark}
							onChange={(e)=>{
								this.setState({remark:e.target.value})
							}}
							placeholder=""
						/>
					</li>
					<li>
						<span className="left">:</span>
						<Select
							value={this.state.is_auto_send}
							onChange={(e)=>this.typeChange('is_auto_send',e)}
							className='sms_select'>
							<Option value={0}></Option>
							{/*<Option value={1}></Option>*/}
						</Select>
					</li>
					{/*<li>*/}
					{/*	<span className="left">:</span>*/}
					{/*	<Input*/}
					{/*		className="liInput"*/}
					{/*	/>*/}
					{/*</li>*/}
				</ul>
				<div className="i_save_btn">
					<Button size="small" type="primary" onClick={this.submit} ></Button>
				</div>
			</Modal>
		);
	}
}

export default NewModule;
