import React from 'react';
import {withRouter} from 'react-router-dom'
import {Button, Tag} from "antd";
import {goodDetails} from "../../../api/goods/goods";
import './shopDetail.sass'
import {searchJson} from "../../../utils/dataStorage";
class GoodDetails extends React.Component{

	state = {
		data:{}
	};


	componentDidMount() {
		goodDetails({searchJson: searchJson({product_id: this.props.location.state.id})}).then(r=>{
			if(r.batch.length){
				let batch = '';
				r.batch.forEach((item,index)=>{
					if(index !== r.batch.length - 1){
						batch += `${item},`
					} else {
						batch += `${item}`
					}
				});
				r.batch  = batch;
			}
			this.setState({data:r})
		}).catch(_=>{})
	}
	
	goBack = () => {
		this.props.history.push({pathname: this.props.location.state.path,state:{current: this.props.location.state.current}})
	};

	render(){
		const {data} = this.state;
		return (
			<div>
				<div className="u_top">
					<div className="u_header">
						<span></span>
						<Button type="default" size="small" onClick={this.goBack}></Button>
					</div>
					<div className="u_body_one">
						<ul className="u_body_top">
							<li className="firstChild"><img src={data.thumbnail} alt=""/></li>
							<li >
								<p>{data.name}</p>
								<p>{data.barcode}</p>
								<p>{data.property_desc}</p>
								<p>{data.category_desc}</p>
							</li>
							<li>
								{/*<p>{data.spec}</p>*/}
								<p>{data.retail_price}</p>
								<p>{data.market_price}</p>
								<p>{data.cost_price}</p>
							</li>
							<li>
								<p>{data.pv}</p>
								<p>{data.batch}</p>
								<p>{data.keep_mode_desc}</p>
								<p>111</p>
							</li>
							<li className="goodDesp">
								<p>
									<span></span>
									<span>{data.intro}</span>
									</p>
								<p>
									<span></span>
									<span>{data.desc}</span>
								</p>
								<p>
									<span></span>
									<span>{data.share_desc}</span>
								</p>
							</li>
						</ul>
					</div>
					
				</div>
				<div className="u_body_three">
					<ul className="u_body_three_list">
						<li>
							<div className="li_header">
								
							</div>
							<div className="li_body">
								<ul>
									<li className="ranking">
										<h4>
											:
											<span>{data.total_sales}</span>
										</h4>
									</li>
									<li  className="ranking">
										<h4>
											:
											<span>{data.total_order_product_sales}</span>
										</h4>
									</li>
									<li  className="ranking">
										<h4>
											:
											<span>{data.total_preorder_sales}</span>
										</h4>
									</li>
								</ul>
								
							
							</div>
						</li>
						<li>
							<div className="li_header">
								
							</div>
							<div className="li_body">
								<ul>
									<li  className="ranking">
										<h4>
											:
											<span>{data.ranking}</span>
										</h4>
									
									</li>
									<li  className="ranking">
										<h4>
											:
											<span>{data.category_ranking}</span>
										</h4>
									</li>
									
								</ul>
							</div>
						</li>
					</ul>
				</div>
				<div className="u_body_groups">
					<div className="group_header">
						
					</div>
					<div className="group_tags">
						{
							data.groups&&data.groups.length?(
								this.state.data.groups.map(item=>{
									return <Tag closable key={item.id} onClose={()=>this.removeGroup(item.id)}>
										{item.name}
									</Tag>
								})
							):''

						}
					</div>
				</div>
			</div>
		)
	}
}
export default withRouter(GoodDetails)
