import React, {Component} from 'react';
import {Modal, Table} from "antd";
import CustomPagination from "../../../../components/Layout/Pagination";
import {userCouponRecords} from "../../../../api/user";

class CouponRecords extends Component {
	constructor(props) {
		super(props);
		this.state = {
			api:userCouponRecords,
			params:{
				status:0
			},
			data:[],
		};
		this.child = React.createRef()
	}
	
	
	
	handleCancel = () => {
		this.props.onClose()
	};
	
	// 
	paginationChange = (list) =>{
		this.setState({data:list})
	};
	
	render() {
		const columns = [
			{
				title: '',
				dataIndex: 'coupon_name'
			},
			{
				title: '',
				dataIndex: 'benefit',
				render: (text,record) => (
					<span>{text}</span>
				)
			},
			{
				title: '',
				dataIndex: 'balance',
				render: (text,record) => {
					for (let key in record['deliverable_desc']) {
						return record['deliverable_desc'][key]
					}
				}
			},
			{
				title: '',
				dataIndex: 'received_at',
			},
			{
				title: '',
				dataIndex: 'invalid_at',
			},
		];
		return (
			<div>
				<Modal
					title=""
					maskClosable={false}
					width={1000}
					visible={this.props.visible}
					onCancel={this.handleCancel}
					onOk={this.check}
					footer={null}
				>
					<div className="chart" id="s_chart">
						<Table
							columns={columns}
							rowKey={record => record.id}
							pagination={false}
							dataSource={this.state.data}
						/>
					</div>
					<div className="pagination">
						<CustomPagination
							api={this.state.api}
							id={this.props.userId}
							ref={this.child}
							text={''}
							valChange={this.paginationChange}
							params={this.state.params}
						/>
					</div>
				</Modal>
			</div>
		);
	}
}

export default CouponRecords;
