import request from '../utils/request.js'
// 
export function admins(params) {
	return request({
		url: '/api/backend/admins',
		method: 'get',
		params: params
	})
}
//
export function addAdmins(params) {
	return request({
		url: '/api/backend/admins',
		method: 'post',
		data: params
	})
}
// /api/admins/delete
export function adminDelete(params) {
	return request({
		url: '/api/backend/admins/delete',
		method: 'delete',
		params: params
	})
}
// /api/admins/{admin}/changeRole   /api/admins/{admin}/setRoles
export function changeRole(params,id) {
	return request({
		url: `/api/backend/admins/${id}/setRoles`,
		method: 'post',
		data: params
	})
}


