import React from "react";
import {message, Modal, Select} from "antd";
import '../BreakfastOrder/css/saleRange.sass'
import {shops} from "../../../api/shops/shopManage";
import {groups} from "../../../api/shops/groups";
import {setRange} from "../../../api/goods/goods";

export default class SaleRange extends React.Component{
	constructor(props) {
		super(props);
		this.state = {
			data:[],  // 
			selectedItems:[],
			selectedGroupItems:[],
			selectedGroupShopsItems:[],
			activeShopGroup:'',
			type: undefined,
			opt:undefined,
			where:undefined
		}
	}
	
	componentDidMount() {
		groups({}).then(r=>{
			this.setState({selectedGroupItems:r.data})
		})
	}
	
	onShopGroupChange = (e) =>{
		this.setState({where:[]});
		let api = '';
		api = e == 'shop'?shops:groups;
		api({}).then(r=>{
			this.setState({type:e,data:r.data})
		});
		
	};
	
	onShopChange = selectedItems =>{
		this.setState({where:selectedItems})
	};
	onTypeChange = opt =>{
		this.setState({opt})
	};
	
	handleCancel = () =>{
		this.props.onCancel();
	};
	
	handleSubmit = () =>{
		if(!this.state.where.length){
			message.error('');
			return
		}
		if(!this.state.opt){
			message.error('');
			return;
		}
		setRange({
			type:this.state.type,
			opt:this.state.opt,
			where:this.state.where
		},this.props.rangeId).then(r=>{
			message.success(r.message)
			this.handleCancel()
		}).catch(_=>{})
	};
	
	render() {
		return (
			<div className="saleRange">
				<Modal
					title=""
					width={1000}
					visible={this.props.visible}
					onCancel={this.handleCancel}
					maskClosable={false}
					okText=""
					cancelText=""
					onOk={this.handleSubmit}
				>
					<div className="saleRangeBox">
						<Select
							defaultActiveFirstOption={false}
							style={{ width: 300 }}
							onChange={this.onShopGroupChange}
							value={this.state.type}
							placeholder=""
						>
							<Select.Option value="shop"></Select.Option>
							<Select.Option value="group"></Select.Option>
						</Select>
						<Select
							defaultActiveFirstOption={false}
							style={{ width: 82,marginLeft:5 }}
							onChange={this.onTypeChange}
							placeholder= ''
						>
							<Select.Option value="in"></Select.Option>
							<Select.Option value="not in"></Select.Option>
						</Select>
						<Select
							defaultActiveFirstOption={false}
							mode="tags"
							value={this.state.where}
							placeholder=''
							className='selectedBox tagBox'
							onChange={this.onShopChange}
							allowClear
							optionFilterProp="children"
							optionLabelProp="label"
							filterOption={(input, option) =>
								option.props.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
							}
						>
							{this.state.data.map(item => (
								<Select.Option key={item.id+""} label={item.name} value={item.id+''}>
									{item.name}
								</Select.Option>
							))}
						</Select>
					</div>
					
				</Modal>
			</div>
		)
	}
	
}
